  fill: white;
}
.eye.active {
  fill: grey;
}

.arrow_down {
  fill: #fff;
  transition: 0.3s;
}
.arrow_down.active {
  transform: rotate(180deg);
}

.heart {
  fill: #ad7955;
  width: 31px;
  height: 21px;
}

.search {
  width: 18px;
  height: 18px;
  fill: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.search:hover {
  transform: rotate(5deg);
}

.ring {
  width: 16px;
  height: 20px;
  fill: #fff;
  cursor: pointer;
  transition: 0.3s;
}
.ring:hover {
  transform: rotate(5deg);
}

.burger {
  width: 18px;
  height: 14px;
  cursor: pointer;
  fill: #fff;
  transition: 0.3s;
}
.burger:hover {
  fill: grey;
}

.cross {
  width: 18px;
  height: 19px;
  cursor: pointer;
  fill: #fff;
  transition: 0.3s;
}
.cross:hover {
  fill: grey;
}

.pencil {
  height: 20px;
  width: 20px;
  fill: #fff;
}
.pencil:hover {
  fill: grey;
}

.exit {
  height: 20px;
  width: 20px;
  fill: #fff;
}
.exit:hover {
  fill: grey;
}

.plus {
  height: 20px;
  width: 20px;
}

.dots {
  width: 5px;
  height: 20px;
  fill: white;
}

.dotsBig {
  width: 6px;
  height: 20px;
  fill: #ad7955;
}

.small-cross {
  width: 12px;
  height: 12px;
  cursor: pointer;
